@import "common/mixins";
@import "common/swiper";

$url_common: "../js/webmap/images/";
$url_common_app: "../js/webapp/images/";
// $url_common: '../images/';
// $url_common_app: '../images/';

#alertDimmed,
#alertPopBox {
  display: none !important;
}
.popup {
  .cont {
    max-height: calc(100vh - 150px);
    overflow: auto;
  }
}
.webmap {
  .styleBox-button {
    width: 36px;
    height: 36px;
    color: transparent;
    font-size: 0;
    padding: 0;
    background: url($url_common+"common/ico_style.png") no-repeat center;
  }
}
.test_btn_list {
  position: absolute;
  top: 100px;
  left: 100px;
  right: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 8px;
  button {
    background-color: #000;
    height: 36px;
    padding: 0 10px;
    color: #fff;
    border-radius: 4px;
    white-space: nowrap;
  }
}

.mui_link {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  a {
    height: 35px;
    line-height: 35px;
    box-sizing: border-box;
    border: 1px solid rgba(47, 85, 151, 1);
    color: rgba(47, 85, 151, 1);
    font-size: 16px;
    padding: 0 10px;
    border-radius: 4px;
    &.mui_active {
      background: rgba(47, 85, 151, 1);
      font-family: "Pretendard Bold";
      color: #ffffff;
    }
  }
}

.modal_tooltip {
  width: 370px;
  box-sizing: border-box;
  position: absolute;
  overflow: visible;
  top: 200px;
  left: 100px;
  padding: 50px 11px 20px;
  background: #fff;
  .tabContWrap.type01 {
    .tabCont {
      border: 0 none;
    }
    .table {
      max-height: 376px;
      overflow: auto;
      &.type02 {
        max-height: 331px;
      }
      &::-webkit-scrollbar {
        width: 14px;
        background-color: rgba(233, 233, 233, 1);
      }
      &::-webkit-scrollbar-thumb {
        background-color: rgba(217, 217, 217, 1);
        border-radius: 7px;
      }
    }
  }
  .section {
    padding-top: 20px;
  }
  .arrow {
    display: block;
    width: 44px;
    height: 24px;
    background: url($url_common + "popup/arrow.png") no-repeat center;
    bottom: -24px;
    position: absolute;
    left: 50%;
    margin-left: -22px;
  }
  .close{
    width: 40px;
    height: 40px;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 0;
    color: transparent;
    background: url($url_common+"popup/ico-card-close.png") no-repeat center;
  }
  .mui_link{
    overflow-x: auto;
    word-break: break-all;
    padding-bottom: 10px;
    a {
      white-space:nowrap;
    }
    &::-webkit-scrollbar {
      width: 14px;
      height: 8px;
      background-color: rgba(233, 233, 233, 1);
    }
    &::-webkit-scrollbar-thumb {
      background-color: rgba(217, 217, 217, 1);
      border-radius: 7px;
    }
  }
}
.mui_form_field {
  select {
    height: 30px;
  }
  .form_body {
    display: block;
    & ~ .form_body {
      margin-top: 24px;
    }
  }
  .select {
    display: flex;
    align-items: center;
    .label {
      width: 108px;
      & + select {
        padding-right: 32px;
        width: 99%;
      }
    }
    &_group {
      display: flex;
      gap: 2px;
      select {
        flex: 1;
      }
    }
  }
  .btns {
    display: flex;
    justify-content: space-between;
    gap: 6px;
    button {
      height: 36px;
      color: #fff;
      border-radius: 4px;
      width: 206px;
      &.write {
        width: 36px;
        color: transparent;
        font-size: 0;
        background: rgba(47, 85, 151, 1)
          url($url_common + "popup/ico_write.png") no-repeat center;
      }
      &.search {
        background: rgba(47, 85, 151, 1);
        span {
          display: inline-flex;
          padding-right: 28px;
          background: url($url_common + "popup/ico_search.png") no-repeat right
            center;
          line-height: 36px;
        }
      }
      &.refresh {
        width: 36px;
        color: transparent;
        font-size: 0;
        background: rgba(47, 85, 151, 1)
          url($url_common + "popup/ico_refresh.png") no-repeat center;
      }
    }
  }
}
.data_table {
  .txt {
    display: inline-flex;
    padding-left: 11px;
    position: relative;
    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      height: 17px;
      width: 1px;
      background: rgba(217, 217, 217, 1);
    }
  }
  th,
  td {
    padding: 16px 0 17px;
  }
}

.movie_pop {
  .tabContWrap.type01 .tabCont{
    padding: 0 12px;
  }
  .mui_form_field {
    padding: 20px 12px;
  }
  .table_sec {
    padding: 0 12px;
    box-sizing: border-box;
  }
  .movie_list {
    margin-top: 24px;
    & > .title {
      font-size: 16px;
      color: #000;
    }
    .list {
      margin-top: 10px;
      border: 1px solid rgba(238, 241, 248, 1);
      padding: 12px 10px;
      display: flex;
      justify-content: space-between;
      .title {
        font-size: 15px;
        color: rgba(85, 85, 85, 1);
      }
      .btns {
        button {
          width: 18px;
          height: 18px;
          & ~ button {
            margin-left: 20px;
          }
          &.link {
            background: url($url_common + "popup/ico_link.png") no-repeat right
              center;
          }
          &.delete {
            background: url($url_common + "popup/ico_x.png") no-repeat right
              center;
          }
        }
      }
    }
  }
  .btn_wrap {
    text-align: center;
    padding: 24px 0 20px;
    button {
      min-width: 96px;
      height: 36px;
      box-sizing: border-box;
      background: rgba(47, 85, 151, 1);
      font-size: 16px;
      color: #fff;
      border-radius: 4px;
    }
  }
}
.btn {
  &.orange2 {
    background-color: #ff4b00;
  }
}
.cScroll {
  .mui_form_field,
  .table_sec {
    padding-left: 0;
    padding-right: 0;
  }
}
.btn_sec {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 40px;
  padding-bottom: 10px;
  &_mini {
    display: flex;
    justify-content: space-between;
  }
}
.select_widget {
  padding: 20px;
  .mui_checkbox_group {
    display: flex;
    gap: 10px;
    margin-top: 10px;
    flex-wrap: wrap;
    .mui_checkbox {
      position: relative;
      display: flex;
      align-items: center;
      border: 1px solid rgba(233, 233, 233, 1);
      width: 55px;
      height: 72px;
      border-radius: 4px;
      justify-content: center;
      input {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        &:checked + label {
          border-color: rgba(67, 106, 235, 1);
          .ico {
            img {
              &:nth-child(1) {
                display: none;
              }
              &:nth-child(2) {
                display: inline;
              }
            }
          }
        }
      }
      label {

        display: block;
        box-sizing: border-box;
        padding: 4px 0;
        text-align: center;
        
        font-size: 10px;
        .ico {
          height: 26px;
          display: block;
          img {
            &:nth-child(2) {
              display: none;
            }
          }
        }
        .text {
          display: block;
          margin-top: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          line-height: 12px;
        }
      }
    }
  }
  .group {
    & ~ .group {
      margin-top: 20px;
    }
  }
}

.login {
  padding: 40px 20px;
  @include respondTo("tablet"){
    padding-top: 56px;
  }
  .logo {
    text-align: center;
    padding-bottom: 20px;
    img {
      @include respondTo("tablet"){
        width: 220px;
      }
    }
  }
  .input {
    display: block;
    border: 1px solid #f9f9f9;
    border-radius: 4px;
    padding-left: 66px;
    background: url($url_common + "widget/ico_pw.png") no-repeat 12px center;
    &:nth-child(2) {
      background: url($url_common + "widget/ico_id.png") no-repeat 12px center;
      @include respondTo("tablet"){
        background: none;
      }
    }
    @include respondTo("tablet"){
      background: none;
      padding-left: 10px;
      border-color: #e9e9e9;
    }
    input {
      border: 0 none;
      height: 40px;
      width: 100%;
      outline: 0;
      padding: 0;
      &::placeholder {
        color: #e9e9e9;
        @include respondTo("tablet"){
          color: #555;
        }
      }
      @include respondTo("tablet"){
        height: 50px;
      }
    }
    & ~ .input {
      margin-top: 10px;
    }
  }
  .link {
    text-align: center;
    margin-top: 20px;
    a {
      display: inline-flex;
      font-family: "Pretendard Bold";
      color: #333;
      font-size: 12px;
      position: relative;
      & ~ a {
        margin-left: 10px;
        padding-left: 11px;
        &::after {
          content: "";
          display: block;
          width: 1px;
          background: #333;
          position: absolute;
          top: 2px;
          left: 0;
          bottom: 2px;
        }
      }
      @include respondTo("tablet"){
        font-size: 16px;
        font-family: "Pretendard";
      }
    }
  }
  .btn_sec {
    margin-top: 20px;
    padding-bottom: 0;
    .btn {
      background: #436aeb;
      width: 114px;
      height: 45px;
      justify-content: center;
      @include respondTo("tablet"){
        width: 100%;
        height: 54px;
        font-size: 18px;
      }
    }
  }
}
.setstyle {
  padding-top: 12px;
  .chk_group {
    display: flex;
    .chk {
      width: 140px;
    }
  }
  .chk {
    display: flex;
    align-items: center;
    input {
      margin: 0;
    }
    label {
      display: inline-block;
      border: 0 none;
      width: auto;
      margin-left: 10px;
    }
  }
  input[type="range"] {
    // -webkit-appearance: none;
    height: 2px;
    // background: #E1E1E1;
    width: 100%;
    box-sizing: border-box;
    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      background: rgba(67, 106, 235, 1);
      width: 10px;
      height: 10px;
      border-radius: 5px;
    }
  }
}
.color_pick_opacity {
  display: flex;
  margin-top: 12px;
  gap: 0 40px;
  .color_pick {
    display: flex;
    gap: 10px;
    align-items: center;
    label {
      white-space: nowrap;
    }
  }
  .opacity_pick {
    display: flex;
    width: 100%;
    .range {
      width: 100%;
      padding-right: 10px;
    }
    .inpu {
      white-space: nowrap;
      .text {
        margin-left: 10px;
      }
      input {
        width: 50px;
        height: 27px;
      }
    }
  }
}
.measurement_cont {
  padding: 40px 24px;
  p.addr1,
  p.addr2 {
    font-size: 18px;
    color: #2e2e2e;
    span.title {
      display: inline-flex;
      width: 110px;
      box-sizing: border-box;
      position: relative;
      padding-left: 13px;
      &::after {
        content: "";
        display: block;
        width: 3px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        background: #8faadc;
      }
    }
  }
  p.addr2 {
    margin-top: 10px;
    span.title {
      &::after {
        background: #ff4b00;
      }
    }
  }
  .select {
    margin-top: 10px;
    padding-bottom: 10px;
    select {
      width: 100%;
    }
  }
  .table_group{
    margin-top: 10px;
    .title{
      font-family: "Pretendard Bold";
      font-size: 14px;
      color: #555;
      padding-bottom: 10px;
      .num {
        margin-left: 20px;
        font-family: "Pretendard";
      }
    }
  }
}
.font_settiing {
  padding-top: 10px;
  .select {
    display: flex;
    gap: 10px;
    select {
      flex: 1;
      height: 27px;
    }
  }
  .select_type {
    margin-top: 20px;
    display: flex;
    .label {
      font-family: "Pretendard Bold";
      font-size: 16px;
      width: 80px;
    }
    button {
      font-size: 16px;
      & ~ button {
        margin-left: 20px;
      }
      &.bold {
        font-family: "Pretendard Bold";
      }
      &.underline {
        text-decoration: underline;
      }
      &.italic {
        font-style: italic;
      }
    }
  }
}
.select_widget_layer {
  .btn_sec {
    margin-top: 24px;
    padding-bottom: 20px;
  }
}



/* userMenu */
/* line 395, scss/common.scss */

#userMenu {
  position: absolute;
  top: 15px;
  right: 100px;
  display: flex;
  z-index: 20;

  .searchArea {
    position: relative;
    display: flex;
    margin-right: 18px;
    select {
      width: 96px;
      height: 38px;
      margin-right: 1px;
      padding-left: 9px;
      text-indent: 0;
      color: #555;
      font-size: 14px;
      border: 0;
      box-shadow: 0.5px 0.9px 4px 0 rgba(0, 0, 0, 0.27);
      border-radius: 3px;
    }

    .inputBox {
      position: relative;
      width: 230px;
      box-shadow: 0.5px 0.9px 4px 0 rgba(0, 0, 0, 0.27);
    }

    input[type="text"] {
      width: 100%;
      height: 38px;
      border: 0;
      padding: 0 45px 0 15px;
      border-radius: 3px;
    }

    .btnSearch {
      position: absolute;
      right: 10px;
      top: 50%;
      width: 21px;
      height: 22px;
      transform: translateY(-50%);
      background: url($url_common+"common/ico-search.png") no-repeat;
    }
  }

  .userInfo {
    display: flex;
    align-items: center;
    gap: 10px;
    .top_widgets{
      button {
        width: 32px;
        height: 32px;
        border-radius: 16px;
        background: #fff  url($url_common+"widget/ico_member.png") no-repeat center;
        &.current_pos{
          background-image: url($url_common+"widget/ico_pos.png");
        }
        &.go_home{
          background-image: url($url_common+"widget/ico_home.png");
        }
      }
    }
    .user {
      position: relative;
      margin-right: 7px;

      .btnUser {
        width: 33px;
        height: 33px;
        border-radius: 50%;
        background: #2F5597 url($url_common+"common/ico-user.png") no-repeat center;
        box-shadow: 0.5px 0.9px 4px 0 rgba(0, 0, 0, 0.27);
      }
    }

    .alarm {
      position: relative;
      .btnAlarm {
        position: relative;
        width: 33px;
        height: 33px;
        border-radius: 50%;
        background: #2F5597 url($url_common+"common/ico-alarm.png") no-repeat -4px -4px;
        box-shadow: 0.5px 0.9px 4px 0 rgba(0, 0, 0, 0.27);
        &.loading .new {
          display: none;
        }
      }
      .btnAlarmDiv.loading .alarmLoading {
        animation: 2s loading linear infinite;

        &:before {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          content: '';
          display: block;
          width: 9px;
          height: 13px;
          z-index: 10;
          background: url($url_common+"common/ico-alarm-loading-2.png") no-repeat center;
          animation: 1s loading2 linear infinite alternate;
        }
      }
    }

    .new {
      position: absolute;
      left: 22px;
      top: -3px;
      z-index: 10;
      height: 14px;
      line-height: 11px;
      padding: 0 2px;
      font-size: 10px;
      color: #fff;
      font-family: "Pretendard Bold";
      border-radius: 3px;
      background: #f85959;

      &.type02 {
        border-radius: 50px;

        &:after {
          bottom: -2px;
        }
      }
    }

    .userPop {
      display: none;
      position: absolute;
      right: 0;
      top: 43px;
      z-index: 10;
      min-width: 100px;
      word-break: keep-all;
      background: #2f5597;
      border-radius: 3px;
      box-shadow: 0.5px 0.9px 4px 0 rgba(0, 0, 0, 0.27);

      &:before {
        position: absolute;
        right: 12px;
        top: -3px;
        display: block;
        content: '';
        border-left: 5px solid #2f5597;
        border-right: 5px solid transparent;
        border-top: 5px solid #2f5597;
        border-bottom: 5px solid transparent;
        transform: rotate(45deg);
      }

      .inner {
        padding: 10px 12px;
      }

      .position {
        display: block;
        color: #ffffff;
        opacity: .4;
        font-family: "Pretendard";
        font-size: 12px;
        font-weight: normal;
      }

      .name {
        display: block;
        margin-bottom: 5px;
        color: #fff;
        font-size: 17px;
        font-family: "Pretendard Bold";

        span {
          font-size: 12px;
          font-weight: normal;
        }
      }

      ul {
        padding-top: 10px;
        border-top: 1px solid rgba(255, 255, 255, 0.4);

        li {
          position: relative;
          margin-bottom: 5px;
          color: #fff;
          font-size: 13px;
          font-family: "Pretendard";

          .new {
            position: absolute;
            left: 0;
            top: 1px;
            z-index: 10;
            width: 16px;
            height: 16px;
            line-height: 16px;
            padding: 0;
            border-radius: 50px;
            margin-right: 4px;
            font-size: 10px;
            text-align: center;
            background: #f85959;
          }

          &:last-of-type {
            margin-bottom: 0;
          }

          > a {
            display: flex;
            align-items: center;
          }
        }
      }

      i.ico {
        display: inline-block;
        width: 15px;
        height: 15px;
        margin-right: 4px;

        &.alarm {
          background-image: url($url_common+"common/ico-user-alarm.png");
        }

        &.myPage {
          background-image: url($url_common+"common/ico-user-mypage.png");
        }

        &.logout {
          background-image: url($url_common+"common/ico-user-logout.png");
        }
      }
    }

    .alarmPop {
      display: none;
      position: absolute;
      right: 0;
      top: 43px;
      width: 320px;
      box-shadow: 0px 2px 6px 0 rgba(0, 0, 0, 0.2);

      .btnPopClose {
        position: absolute;
        right: 11px;
        top: 11px;
        width: 20px;
        height: 20px;
        background: url($url_common+"common/ico-pop-close.png") no-repeat;
      }

      .head {
        height: 44px;
        background: #f85959;
        border-radius: 3px 3px 0 0;

        .titPop {
          display: block;
          line-height: 44px;
          color: #fff;
          font-size: 20px;
          font-weight: normal;
          text-indent: 44px;
          font-family: "Pretendard";
          background: url($url_common+"common/ico-alarm-head.png") no-repeat 17px center;
        }
      }

      .cont {
        background: #f6f6f6;

        .inner {
          padding: 24px 23px 15px;
        }

        .dayBox {
          position: relative;
          margin-bottom: 26px;

          &:before {
            position: absolute;
            left: 6px;
            top: 17px;
            width: 1px;
            height: calc(100% + 11px);
            display: block;
            content: '';
            border-left: 1px dotted #d2d2d2;
          }

          .date {
            margin-bottom: 19px;
            text-indent: 21px;
            color: #657389;
            font-size: 14px;
            font-family: "Pretendard Bold";
            background: url($url_common+"common/ico-alarm-cal.png") no-repeat left center;

            span {
              font-size: 13px;
              font-weight: normal;
            }

            &.today {
              color: #f85959;
            }
          }

          &:last-of-type {
            margin-bottom: 15px;

            &:before {
              height: calc(100% - 60px);
            }
          }
        }

        .btnArea .btn {
          height: 30px;
          font-size: 14px;
        }

        .log {
          position: relative;
          margin-left: 20px;
          margin-bottom: 10px;
          background: #fff;
          border: 1px solid #c0c5cc;
          border-radius: 4px;

          &:before {
            position: absolute;
            left: -8px;
            top: 14px;
            display: block;
            content: '';
            width: 8px;
            height: 9px;
            background: url($url_common+"common/ico-alarm-before.png") no-repeat;
          }

          &:after {
            position: absolute;
            left: -17px;
            top: 17px;
            display: block;
            content: '';
            width: 5px;
            height: 5px;
            border-radius: 3px;
            border: 1px solid #69778c;
            box-sizing: border-box;
            background: #fff;
          }

          .btnGroup {
            position: absolute;
            right: 10px;
            top: 10px;
            display: flex;
            justify-content: flex-end;

            button {
              width: 20px;
              height: 20px;
              background-position: center;
              background-repeat: no-repeat;

              &.btnLogAdd {
                background-image: url($url_common+"common/ico-alarm-add.png");
              }

              &.btnLogRemove {
                margin-left: 5px;
                background-image: url($url_common+"common/ico-alarm-remove.png");
              }
            }
          }

          .inner {
            padding: 10px;

            p {
              display: flex;
              align-items: center;

              strong {
                display: inline-block;
                max-width: 150px;
                color: #333333;
                font-size: 15px;
                overflow: hidden;
                text-overflow: ellipsis;
                word-break: break-all;
                white-space: nowrap;
                font-family: "Pretendard Bold";
              }

