.switch{
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  .mui_checkbox{
    display: inline-block;
    vertical-align: top;
    margin-right: 0;
    input{
      opacity: 0;
      width: 0;
      height: 0;
      position: absolute;
      left: -9999em;
      &:checked + label{
        &::after{
          left: 85px;
        }
        span {
          color: #888;
          &:last-child {
            color: #fff;
          }
        }
      }
    }
    
    label{
      position: relative;
      display: flex;
      width: 166px;
      height: 36px;
      font-size: 12px;
      cursor: pointer;
      
      span {
        line-height: 36px;
        position: relative;
        z-index: 2;
        color: #888;
        flex: 1;
        text-align: center;
        &:first-child {
          color: #fff;
        }
      }
      &::before{
        content: '';
        width: 166px;
        height: 36px;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 18px;
        transition: background .2s;
        border: 1px solid #E9E9E9
      }
      &::after{
        content: '';
        width: 80px;
        height: 30px;
        border-radius: 15px;
        background:#2F5597;
        top: 4px;
        left: 3px;
        position: absolute;
        transition: left .2s;
      }
    }
  }
}
